import React, { Component } from 'react';



export default class Help extends Component {
	constructor() {
		super();
		this.state = {
			selected: null,
			submitted: false
		}
	}
	render() {
		return(
			<div>

		        <div className="main-body mt-5 pt-5">

		          <div className="container custom-container pt-5 mt-4">
		            <div className="row">
		            	<div className="col-12 col-lg-8 offset-lg-2 d-flex flex-column align-items-center">
		            		<p className="font-secondary mb-0" style={{ fontSize : '1.5rem', fontWeight: '500'}}>Holla!</p>
		            		<p className="font-secondary mb-4" style={{ fontSize : '1.5rem', fontWeight: '500'}}>How can we Help You?</p>
		            		<div className="w-100 d-flex mb-5" style={{ flexWrap: 'wrap' }}>
		            			<div 
		            				className={`cursor-pointer transition d-flex flex-column align-items-center justify-content-center ${this.state.selected == 1 ? 'bg-white shadow-card' : 'bg-light text-light' } border-dark p-3 mb-3 mb-sm-0 mr-sm-4`}
		            				style={{ flex: '1', borderRadius: '3px', height: '200px'}}
		            				onClick={() => this.setState({ selected : 1 })}>
		            				<img src={require('./../assets/images/help1.svg')} style={{ height: '80px'}} className={`mb-4 ${this.state.selected == 1 ? '' : 'opacity-2'} `} />
		            				<div className="text-center">Deciding Painting for You</div>
		            			</div>
		            			<div className="d-sm-none w-100"></div>
		            			<div
		            				className={`cursor-pointer transition d-flex flex-column align-items-center justify-content-center ${this.state.selected == 2 ? 'bg-white shadow-card' : 'bg-light text-light' } border-dark p-3 mr-4`}
		            				style={{ flex: '1', borderRadius: '3px', height: '200px'}}
		            				onClick={() => this.setState({ selected : 2 })}>
		            				<img src={require('./../assets/images/help2.svg')} style={{ height: '80px', paddingBottom: '15px', paddingTop: '15px'}} className={`mb-4 ${this.state.selected == 2 ? '' : 'opacity-2'} `} />
		            				<div className="text-center">Order Customisable Painting</div>
		            			</div>
		            			<div
		            				className={`cursor-pointer transition d-flex flex-column align-items-center justify-content-center ${this.state.selected == 3 ? 'bg-white shadow-card' : 'bg-light text-light' } border-dark p-3`}
		            				style={{ flex: '1', borderRadius: '3px', height: '200px'}}
		            				onClick={() => this.setState({ selected : 3 })}>
		            				<img src={require('./../assets/images/help3.svg')} style={{ eight: '80px', paddingBottom: '10px', paddingTop: '10px'}} className={`mb-4 ${this.state.selected == 3 ? '' : 'opacity-2'} `} />
		            				<div className="text-center">Other Problems</div>
		            			</div>
		            		</div>
		            		<div className={`${this.state.selected ? 'px-5 py-3 h-auto' : 'h-0 p-0'} d-flex align-items-center overflow-hidden mb-5 transition`}>
		            			<input type="email" className="form-control mr-3 transition" placeholder="Enter your Email Id for Contact" style={this.state.submitted ? { padding: '0', border: 'none', height : '3rem', width : '0' }:{ height : '3rem', width : '20rem' }} />
		            			<button
		            				className={`btn text-white transition overflow-hidden rounded-circle ${this.state.submitted ? 'bg-green' : 'bg-primary'}`}
		            				style={{ height : '3rem', width : '3rem', transform : 'scale(1.2)' }}
		            				onClick={() => this.setState({ submitted : true, selected : 4 })}>
		            				{
		            					this.state.submitted ?
		            					<i className="ion ion-md-checkmark"/> :
		            					<i className="ion ion-md-arrow-forward"/>
		            				}
		            			</button>
		            			{	
		            				this.state.submitted ?
		            				<p className="mx-3 mb-0">We Will Get Back to you on this Email Id</p> : null
		            			}
		            		</div>
		            		{/*<button className={`btn bg-dark text-white transition ${this.state.selected ? 'px-5 py-3 h-auto' : 'h-0 p-0'} overflow-hidden mb-5`}>Contact me on prokshh@gmail.com</button>*/}

		            		{/*<button className={`btn bg-dark text-white transition ${this.state.selected ? 'px-5 py-3 h-auto' : 'h-0 p-0'} overflow-hidden mb-5`}>Contact me on prokshh@gmail.com</button>*/}
		            		<p className="text-light mb-4">OR</p>
		            		<p className="mb-0 text-light">Contact Us On</p>
		            		<p className="mb-0"><i className="ion ion-md-call mr-2 font-size-large" />0-8285-999-091</p>
		            		<p className="mb-5"><i className="ion ion-md-mail mr-2 font-size-large" />glaze.support@gmail.com</p>
		            		{/*<p>We can Help you with Anything from Selecting any painting to offer you Costomisable Painting or just General Issues. Write us below and we will Get back to You.</p>*/}
		            		{/*<textarea className="form-control w-100 mb-3" style={{ height : '15rem' }} />*/}
		            		{/*<button className="btn bg-dark text-white px-4 py-3">Send</button>*/}
		            	</div>
		            </div>
		          </div>

		        </div>
		    </div>
		);
	}
}


